import './dip.less'
import { Link } from 'react-router-dom'
import { inject, observer } from 'mobx-react'
import React, { useState, useEffect } from 'react'
import Button from '../../components/Button/Button'
import { useAlert } from 'react-alert'
import DeriNumberFormat from '../../utils/DeriNumberFormat'
import ApiProxy from '../../model/ApiProxy'
import backIcon from '../../assets/img/back.svg'
function DipThree({ wallet, lang }) {
  const [value, setValue] = useState()
  const [optionInfo, setOptionInfo] = useState()
  const [sumPower, setSumPower] = useState(0)
  const [winner, setWinner] = useState()
  const [deadline, setDeadline] = useState(false)
  const [disabled, setDisabled] = useState(true)
  const [isOptioned, setIsOptioned] = useState(true)
  const [votingPower, setVotingPower] = useState()
  const alert = useAlert();
  const selectOption = (value) => {
    if (!isOptioned) {
      let radioI = document.getElementById("I")
      let radioII = document.getElementById("II")
      if (value === "1") {
        radioI.click()
      } else {
        radioII.click()
      }
      setValue(value)
    }

  }
  const getUserVotingPower = async () => {
    let res = await ApiProxy.request("getUserVotingPower", [wallet.detail.account])
    setVotingPower(res)
  }
  const getUserVotingResult = async () => {
    let res = await ApiProxy.request("getUserVotingResult", [wallet.detail.account])
    console.log("getUserVotingResult", res)
    if (res.option !== "0") {
      setValue(res.option)
      setIsOptioned(true)
    } else {
      setIsOptioned(false)
    }
  }

  const getVotingResult = async () => {
    let res = await ApiProxy.request("getVotingResult")
    console.log("getVotingResult", res)
    let sum = (+res.option_1) + (+res.option_2)
    let winnerPower = (+res.option_1) > (+res.option_2) ? "1" : "2"
    setWinner(winnerPower)
    setSumPower(sum)
    setOptionInfo(res)
  }

  const getVotingInfo = async () => {
    let res = await ApiProxy.request("getVotingInfo")
    let time = res.find(i => +i.chainId === +wallet.detail.chainId)
    time = time.dedeadline + "000"
    let timestamp = new Date()
    if (timestamp.getTime() >= +time) {
      setDeadline(true)
    } else {
      setDeadline(false)
    }
    console.log("getVotingInfo", timestamp.getTime() >= +time)

  }

  const vote = async () => {
    console.log([wallet.detail.chainId, wallet.detail.account, value])
    let res = await ApiProxy.request("vote", [wallet.detail.chainId, wallet.detail.account, value], { includeResponse: true })
    if (res.success) {
      getUserVotingPower()
      getUserVotingResult()
      getVotingResult()
      alert.success("Success")
    } else {
      alert.error("Failed")
    }

  }
  useEffect(() => {
    if (wallet.isConnected()) {
      getUserVotingPower()
      getUserVotingResult()
      getVotingInfo()
    }
  }, [wallet, wallet.detail])
  useEffect(() => {
    getVotingResult()
  }, [])
  useEffect(() => {
    if (wallet.isConnected() && value && !isOptioned && !deadline) {
      setDisabled(false)
    } else {
      setDisabled(true)
    }
  }, [wallet.detail, value, optionInfo, isOptioned])
  return (
    <div className='dip-box'>
      <div className='back-governance'>
        <Link to='/governance'>
          <div className='icon-back'>
            <img alt='' src={backIcon} />
            <span>{lang['back']}</span>
          </div>
        </Link>
        <div className='poll-detail'>
          {lang['poll-detail']}
        </div>
      </div>
      <div className='dip-box-info'>
        <div className='poll-dip-info'>
          <div className='dip-list-title'>
            <div className='title-text'>
              {lang['dip']}-004
            </div>
            <div className='dip-progress'>
              <div className='executed-bg'></div>
              FINISHED
            </div>
          </div>
          <div className='dip-list-des'>
            Revote for changing the DERI burning mechanism
          </div>
          <div className='dip-time'>
            Target End Time: 2022 Sat. 4th June 4:00 AM UTC (subject to extension, see below for details)
          </div>
        </div>
        <div className='dip-option'>
          <div className={value === "1" ? "option-check option-checked" : "option-check"} onClick={(e) => selectOption("1")} >
            <div className='dip-one-option-title option-title'>
              <div className='option-check-text'>
                <input
                  type='radio'
                  name='option'
                  value='1'
                  id="I"
                  // onChange={event => selectOption("1")}
                  checked={value === "1"}
                />
                <label for="I"></label>
                <span className='option-title-text'>
                  Burn to deadlock address
                </span>
              </div>
              <div className='option-num'>
                2997820 {lang['deri-voting']} (87%)
              </div>
            </div>
            <div className='option-peng-bg' style={{ width: `87%` }}></div>
          </div>
          <div className={value === "2" ? "option-check option-checked" : "option-check"} onClick={() => selectOption("2")} >
            <div className='dip-one-option-title option-title'>
              <div className='option-check-text'>
                <input
                  type='radio'
                  name='option'
                  value='2'
                  id="II"
                  // onChange={event => selectOption("2")}
                  checked={value === "2"}
                />
                <label for="II"></label>
                <span className='option-title-text'>
                  Burn to mining vault (the status quo)
                </span>
              </div>
              <div className='option-num'>
                428618 {lang['deri-voting']} (13%)
              </div>
            </div>
            <div className='option-peng-bg ' style={{ width: `13%` }}></div>
          </div>
          <div className='vote-button'>
            <div className='your-power'>
              {/* Your Voting Power :
              <DeriNumberFormat value={votingPower} decimalScale={2} /> DERI */}
            </div>
            <Button disabled={true} label={lang['vote']} onClick={vote} width='286' fontSize='18' />
          </div>
        </div>
        <div className='dip-option-des-rules'>
          <div className='description-close-open'>
            {lang['description']}
          </div>
          <div className='description-text'>
            <div className='ul-description'>
              Based on the community poll on snapshot proposed by a community member, the Deri team has placed an executive proposal (DIP 4) into the governance voting system. DERI Holders should vote for this proposal if they want to change the current DERI burning mechanism.
              <br></br>
              The option one proposes to burn to deadlock address, thus removing DERI from circulation forever.
              <br></br>
              The option two is the status quo, which is that DERI bought from the secondary market is sent to the mining vault.
              <a target="_blank" href="https://docs.deri.io/library/tokenomics#burning-mechanism">Read more.</a>
            </div>
          </div>

          <div className='vote-rules'>
            <div className='rules-title'>
              {lang['voting-rules']}
            </div>
            <div className='rules-text'>
              <div className='rules-margin'>
                Your voting power is the summation of the following balances in your wallet as of the deadline:
                <ul className='no-margin'>
                  <li>DERI balance</li>
                  <li>the corresponding DERI amount of your DERI-USDT SLP balance on SushiSwap</li>
                  <li>the corresponding DERI amount of your DERI-BUSD CLP balance on PancakeSwap</li>
                  <li>the corresponding DERI amount of your DLT balance in the DERI-based Inno Zone pools (BNB Chain)</li>
                </ul>
              </div>
              <div className='rules-margin'>
                You can choose any of the 3 chains (ETH/BNB Chain/Aribitrum) to sign your vote by the deadline
              </div>
              <div className='rules-margin'>
                Voting Deadline: targeted at 2022 Sat. 4th June 4:00 AM UTC.  When a vote is submitted less than 15 min to the deadline, the deadline will be extended by 15 min. That is, the voting period will continue to extend until there is no vote within the last 15 min.
              </div>
              <div className='rules-margin'>
                A snapshot will be taken at the block, at the deadline, and used to calculate the result. Please note that, if you had voted before the deadline but then had transferred your DERI, SLP, PANCAKE-LP, or DLT before the deadline, it would be the balance at the deadline that counts.
              </div>
            </div>
          </div>
          <div className='supplementary-notes'>
            <div className='notes-title'>
              Reference Poll Link
            </div>
            <div className='notes-text'>
              <a target="_blank" href="https://snapshot.org/#/deri.eth/proposal/0x7671323ba773ebc9043e339abb50faef5655597342aef1cbe23a7e9be85fd3bb">Snapshot</a>
            </div>
          </div>
          <div className='supplementary-notes'>
            <div className='notes-title'>
              Discussion
            </div>
            <div className='notes-text'>
              <a target="_blank" href="https://discord.com/invite/kb8ZbYgp8M">Discord</a>
            </div>
          </div>
        </div>
      </div>
    </div>
  )
}
export default inject('wallet')(observer(DipThree))


